
.edit-view{
  background-color: #f7b11e;
  position: absolute;
  right: 35px;
  top: 55px;
  width: 50px;
  height: 50px;
  border-radius: 25px;
  cursor: pointer;
  z-index: 20000;
}
.edit-view i{
  color: #fff;
  display: block;
  line-height: 50px;
  text-align: center;
  font-size: 22px;
}
.common-document-error{
  position: absolute;
  z-index: 1001;
  right: 35px;
  top: 30px;
  color: red;
}
.common-document-view-mode{
  margin:0 auto;
  display: table;
}
.common-document-view-mode .top{
  height:103px;
  background-repeat:no-repeat;
  width: 736px;
}
.common-document-view-mode .middle{
  width:736px;
  min-height: 845px;
  background-repeat :repeat-y;
  position:relative;
  padding: 20px 50px 20px 50px;
}
.common-document-view-mode .bottom{
  width:736px;
  height:93px;
  background-repeat:no-repeat;
}
.common-document-view-mode .seal{
  position: absolute;
  right: calc(50% - 368px);
  top: 5px;
  width: 152px;
  height: 152px;
  background-repeat:no-repeat;
}
.common-document>.vue-html5-editor{
  margin-top: -20px;
}
.common-document>.vue-html5-editor>.content{
  max-height: initial;
}



