
.task_document_detail_list_item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  width: 100%;
  line-height: 40px;
  border: 1px solid #fff;
  cursor: default;
}
.task_document_detail_list_item .item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    max-width: 100%;
    overflow: hidden;
    padding: 0 20px 0 10px;
}
.task_document_detail_list_item .item .key {
      white-space: nowrap;
      color: #aaa;
}
.task_document_detail_list_item .item .value {
      max-width: 100%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #333;
}



