
.input-item {
  position: relative;
  min-width: 30px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 4px;
}
.input-item .input-message-icon {
    position: absolute;
    overflow: hidden;
    z-index: 100;
    top: 0;
}
.input-item .el-input__inner:focus {
    border: 1px solid #bfcbd9 !important;
}
.input-item .el-input__inner:hover {
    border: 1px solid #bfcbd9 !important;
}



