
.ptit-form-card{
  background-color:white;
  margin-bottom: 20px;
}
.ptit-form-card-header{
  border-bottom: 1px solid #bfcbd9;
  padding:10px;
}
.ptit-form-card-header h3{
  font-size: 18px;
  font-weight: 800;
  line-height: 32px;
  vertical-align: middle;
}
.ptit-form-card-body{
  min-height: 32px;
  padding: 40px;
  font-size: 0;
  letter-spacing: 0px;
  word-spacing: 0px;
}
.ptit-form-card .ptit-form-item{
  display: inline-block;
  min-width: 300px;
}
.ptit-col-3 .ptit-form-item{
  width: 33.33333333%;
}
.ptit-col-6 .ptit-form-item{
  width:50%;
}
.ptit-col-12 .ptit-form-item{
  width:100%;
}
.ptit-col-4 .ptit-form-item{
  width: 25%;
}
.ptit-col-12 .ptit-form-item{
  width:100%;
}
.ptit-form-card .ptit-form-button-item{
  width:auto;
  min-width: auto;
}
.ptit-form-card-header .ptit-form-button-item{
  float: right;
}




