
.endorsement-attachment-upload-default .more,
 .endorsement-attachment-upload-default .el-upload-list--text .el-upload-list__item:first-child{
   margin-top:5px;
}
.endorsement-dialog-uploader .el-upload{
   display: none;
}
.endorsement-attachments .el-dialog{
   padding-bottom: 20px;
}
.endorsement-attachment-upload ul.el-upload-list{
   display: block;
   float: left;
}
.endorsement-attachment-upload a{
   border-bottom: none;
}
.endorsement-attachment-upload label.el-upload-list__item-status-label{
   line-height: inherit;
   padding: 0;
}
.endorsement-attachment-upload .el-upload-list__item{
   margin-left: 10px;
   margin-right: 10px;
}
.endorsement-dialog-uploader  .el-upload-list li{
  width:100%;
}
.ios-file-list{
  display: block;
  float: left;
  list-style-type: none;
}
.ios-file-list li{
  display: block;
  float: left;
  position: relative;
  font-size: 14px;
  margin-right:10px;
}



