
.ptit-image-upload-view img{
  width: 150px;
  display: inline-block;
  max-width: 100%;
  height: 150px;
  padding: 4px;
  line-height: 1.42857143;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  transition: all .2s ease-in-out;
  cursor:pointer;
}



