
.document-list{
  position: absolute;
  top: 109px;
  background-color: #f2f5fa;
  bottom: 0px;
  left:calc(5% + 120px);
  right: 0px;
}
.document-list .document-tab{
  padding-left: 20px;
  height: 100px;
  line-height: 100px;
  vertical-align: middle;
  float: left;
  position: relative;
}
.document-list .document-filter{
  float: right;
  position: relative;
  margin-right: 40px;
  vertical-align: middle;
  padding-top: 10px;
}
.document-list-view{
  position: absolute;
  top: 100px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  overflow-y: auto;
}
.document-item{
  position: relative;
  float: left;
  background-color: white;
  margin-left: 2%;
  padding: 20px;
  margin-bottom: 20px;
  width: 96%;
   height: 110px;
}
.paging-loading{
  position: relative;
  float: left;
  width: 100%;
  line-height: 32px;
  text-align: center;
}
.document-body .document-field:last-child{
  margin-right: 0;
}
.document-body{
  float: left;
}
.document-detail{
  padding:20px;
}
.document-operation{
  float:right;
  margin-top: 20px;
  margin-right: 0px;
}
.document-field{
  display: block;
  float: left;
  min-width: 150px;
  margin-right: 30px;
}
.document-field>span{
  display: block;
  float: left;
  width: 100%;
}
.document-title{
  color:#409BFC;
  font-weight: 800;
  margin-bottom: 10px;
}
.document-tab a{
  padding:10px;
  color:#2c96fd
}
.document-tab a.active{
  font-weight: 800;
  text-decoration: underline;
}
.white_board_0001 {
  position: relative;
  margin-top: 1%;
  margin-bottom: 1%;
  left: 2%;
  width: 96%;
  height: 100%;
  background-color: white;
}
.white_board_0001 > h3 {
  font-size: 14px;
  color: grey;
  position: absolute;
  top: 5%;
  left: 1.5%;
}
.white_board_center {
  position: relative;
  /*left: 22%;*/
  padding-top: 4.7%;
}
.white_board_0001 hr {
  position: absolute;
  top: 13%;
  width: 100%;
  size: 1px;
}



