
.document-header{
  position: relative;
  height:109px;
  width: 100%;
  border-bottom: 1px solid;
  background-color: white;
}
.document-header .company-warp{
  position: relative;
  float: right;
  padding-top:40px;
  margin-right: 40px;
}
.company-logo{
  background-repeat: no-repeat;
  background-position: left center;
  height:108px;
  position: relative;
  float: left;
}




