
.endorsement {
  background-color: white;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: auto;
}
.endorsement-scroll-container {
  position: relative;
  float: left;
  width: 100%;
  min-width: 776px;
}
#endorsement-header {
  font-size: 0;
  letter-spacing: 0px;
  word-spacing: 0px;
}
#endorsement-header .ptit-form-item {
  display: inline-block;
  min-width: 100px;
  font-size: 14px;
}
#endorsement-header .endorsement-attachments {
  padding: 0px 10px;
  width: 100%;
  display: inline-block;
  margin-bottom: 10px;
}
.endorsement-static-content {
  display: inline-block;
  line-height: 32px;
  font-size: initial;
  letter-spacing: 0px;
  word-spacing: 0px;
}
.endorsement-static-content div {
  float: left;
}
.endorsement-static-content .el-dialog__wrapper .el-upload-list li,
.endorsement-static-content .el-dialog__wrapper,
.endorsement-static-content .el-dialog__wrapper div {
  float: unset;
}
.endorsement .operate-area {
  padding: 10px;
  border-bottom: #e5e5e5 solid 1px;
  margin-bottom: 10px;
}
.endorsement .el-tabs__content,
.el-tab-pane {
  height: auto;
  background-color: white;
  padding: 10px;
}
.endorsement-static-content .label {
  margin-right: 10px;
  width: 100px;
  text-align: right;
  display: inline-block;
}
.endorsement-static-content .text {
  margin-right: 20px;
}



