
.options{
  position: absolute;
  border: 1px solid #d1dbe5;
  z-index: 10;
  background-color: #fff;
}
.options li{
  list-style: none;
  font-size: 14px;
  padding: 10px;
  border-bottom: 1px solid #d1dbe5;
  cursor: default;
  width: 70px;
}
.options li:last-child{
  border-bottom: 0;
}
.options .disable{
  background-color:#eef1f6;
  cursor:not-allowed;
}



