
.ptit-form-item-inline .ptit-form-item {
  display: inline-block;
  min-width: 150px;
}
.ptit-form-item label {
  text-align: right;
  vertical-align: middle;
  float: left;
  line-height: 1;
  padding: 8px 12px 9px 0;
  box-sizing: border-box;
  font-weight: normal;
}
.ptit-form-item-content,
.ptit-form-item-default-content {
  position: relative;
  line-height: 32px;
  padding-bottom: 5px;
}
.ptit-form-item-default-content {
  padding-bottom: 18px;
}
.ptit-form-button-item {
  width: auto;
  min-width: initial;
}
.ptit-form-button-item .ptit-form-item-content {
  padding-bottom: 0px;
}
.ptit-form-item {
  margin-bottom: 5px;
  font-size: 14px;
}
.ptit-form-item:before,
.ptit-form-item:after {
  content: '';
  display: table;
}
.ptit-form-item:after {
  clear: both;
}
.ptit-form-item .el-input__inner {
  height: initial;
}
.ptit-form-item .ptit-input-view {
  color: black;
}
.ptit-form-item .el-date-editor,
.ptit-form-item .el-select,
.ptit-form-item .el-autocomplete {
  width: 100%;
}
.ptit-input-view {
  border-bottom: dotted 1px;
}
.ptit-form > h3 {
  margin-left: 10px;
  margin-bottom: 15px;
}
.ptit-form .el-tag {
  margin-left: 5px;
}
.ptit-form > h3 .ptit-form-item {
  display: inline-block;
  vertical-align: middle;
}
.ptit-form-item-error-message {
  margin-top: 1px;
  line-height: 1;
  position: absolute;
}
.ptit-form-item-error-message i {
  margin-right: 1px;
  color: red;
  font-size: 12px;
}
.ptit-input-default input[type='text'] {
  display: inline-block;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #bfcbd9;
  box-sizing: border-box;
  color: #1f2d3d;
  font-size: inherit;
  height: 36px;
  line-height: 1;
  outline: none;
  padding: 3px 10px;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 100%;
}
.ptit-form-item-error input[type='text'] {
  border-color: red;
  border-width: 1px;
}
.ptit-attachment-upload-view a {
  padding: 10px;
}
.el-upload-list li {
  float: left;
  width: auto;
}
.ptit-form-item .el-upload-list--text .el-upload-list__item:first-child {
  margin-top: 5px;
}
.ptit-attachment-upload a {
  border-bottom: 1px dotted;
}
.ptit-attachment-upload-view a {
  color: black;
  margin-right: 10px;
}
.ptit-form-button-item {
  min-width: initial;
  width: auto;
}

