  text-align: center;
  color: white;
  cursor: pointer;
  font-size: 16px;
  border: none;
}

/*筛选按钮-蓝色*/
.b-blue-filter {
  width: 46px;
  height: 41px;
  background-color: #56a8fe;
  border-radius: 5px;
  text-align: center;
  color: white;
