  font-size: 12px;
  border: none;
}

/*筛选按钮-灰色*/
.b-grey-filter {
  width: 46px;
  height: 41px;
  background-color: #b3c4d6;
  border-radius: 5px;
  text-align: center;
  color: white;
  cursor: pointer;
  font-size: 12px;
  border: none;
}

/*查找按钮-灰色*/
.b-grey-search {
  width: 60px;
  height: 41px;
  background-color: #b3c4d6;
  border-radius: 5px;
  text-align: center;
  color: white;
  cursor: pointer;
  font-size: 12px;
  border: none;
}

/*更多选项按钮-白底灰框*/
.b-white-option {
  width: 68px;
  height: 34px;
  background-color: white;
  border-radius: 5px;
  text-align: center;
  color: grey;
  cursor: pointer;
  font-size: 12px;
  border: 1px solid lightgrey;
}

/*小输入框*/
.input-style-small {
  width: 230px;
  height: 40px;
  border: 1px solid lightgrey;
  border-radius: 4px;
  outline: none;
  font-size: 14px;
  color: grey;
  cursor: pointer;
}

/*中输入框*/
.input-style-middle {
  width: 338px;
  height: 60px;
  border: 1px solid lightgrey;
  border-radius: 2px;
  outline: none;
  font-size: 16px;
  color: grey;
  cursor: pointer;
}

/*大输入框*/
.input-style-big {
  width: 500px;
  height: 60px;
  border: 1px solid lightgrey;
  border-radius: 2px;
  outline: none;
  font-size: 16px;
  color: #f7b11e;
  cursor: pointer;
}

/*登录界面输入框*/
.login-input-style {
  width: 400px;
  height: 60px;
  border: 1px solid lightgrey;
  border-radius: 2px;
  outline: none;
  font-size: 16px;
